<template>
	<div class="auth-wrapper auth-v2">
		<b-row class="auth-inner m-0">
			<!-- Brand logo-->
			<b-link class="brand-logo">
				<vuexy-logo />
				<h2 class="brand-text text-primary ml-1">
					SaySS
				</h2>
			</b-link>
			<!-- /Brand logo-->

			<!-- Left Text-->

			<!-- /Left Text-->

			<!-- Login-->
			<b-col lg="5" class="d-flex align-items-center auth-bg px-2 p-lg-5">
				<b-col sm="8" md="6" lg="12" class="px-xl-2 mx-auto">
					<b-card-title title-tag="h2" class="font-weight-bold mb-1">
						 
					</b-card-title>
					<b-card-text class="mb-2">
						
					</b-card-text>

					<!-- form -->
					<validation-observer ref="loginValidation">
						<b-form class="auth-login-form mt-2" @submit.prevent>
							<!-- email -->
							<b-form-group label="" label-for="login-email">
								<validation-provider #default="{ errors }" name="Email" rules="required|email">
									<b-form-input
										id="login-email"
										v-model="input_email"
										:state="errors.length > 0 ? false : null"
										name="login-email"
										placeholder="john@example.com"
									/>
									<small class="text-danger">{{ errors[0] }}</small>
								</validation-provider>
							</b-form-group>
							<b-form-group label="" label-for="login-verify">
								<b-input-group>
									<b-form-input v-model="input_code" placeholder="" />
									<b-input-group-append>
										<b-button variant="outline-primary" @click="sendCode()" :disabled="!show || isPosting">
											{{ show ? '' : count + 's' }}
										</b-button>
									</b-input-group-append>
								</b-input-group>
							</b-form-group>

							<!-- forgot password -->
							<b-form-group>
								<div class="d-flex justify-content-between">
									<label for="login-password"></label>
								</div>
								<validation-provider #default="{ errors }" name="Password" rules="required">
									<b-input-group class="input-group-merge" :class="errors.length > 0 ? 'is-invalid' : null">
										<b-form-input
											id="login-password"
											v-model="input_password"
											:state="errors.length > 0 ? false : null"
											class="form-control-merge"
											:type="passwordFieldType"
											name="login-password"
											placeholder=""
										/>
										<b-input-group-append is-text>
											<feather-icon class="cursor-pointer" :icon="passwordToggleIcon" @click="togglePasswordVisibility" />
										</b-input-group-append>
									</b-input-group>
									<small class="text-danger">{{ errors[0] }}</small>
								</validation-provider>
							</b-form-group>
							<b-form-group label="" label-for="login-verify">
								<b-input-group>
									<b-form-input placeholder="()" />
								</b-input-group>
							</b-form-group>
							<!-- checkbox -->
							<b-form-group>
								<b-form-checkbox id="remember-me" v-model="status" name="checkbox-1">
									
								</b-form-checkbox>
							</b-form-group>

							<!-- submit buttons -->
							<b-button type="submit" :disabled="isPosting" variant="primary" block @click="reg">
								<b-spinner small v-show="isPosting" type="grow" />
								{{ isPosting ? '' : '' }}
							</b-button>
						</b-form>
					</validation-observer>

					<b-card-text class="text-center mt-2">
						<span>? </span>
						<b-link :to="{ name: 'Login' }">
							<span>&nbsp;</span>
						</b-link>
					</b-card-text>
				</b-col>
			</b-col>
			<b-col lg="7" class="d-none d-lg-flex align-items-center p-5">
				<div class="w-100 d-lg-flex align-items-center justify-content-center px-5">
					<b-img fluid :src="imgUrl" alt="Login V2" />
				</div>
			</b-col>
			<!-- /Login-->
		</b-row>
	</div>
</template>

<script>
/* eslint-disable global-require */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
	BRow,
	BCol,
	BLink,
	BFormGroup,
	BFormInput,
	BInputGroupAppend,
	BInputGroup,
	BFormCheckbox,
	BCardText,
	BCardTitle,
	BImg,
	BForm,
	BButton,
	BSpinner
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import store from '@/store/index'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	components: {
		BRow,
		BCol,
		BLink,
		BFormGroup,
		BFormInput,
		BInputGroupAppend,
		BInputGroup,
		BFormCheckbox,
		BCardText,
		BSpinner,
		BCardTitle,
		BImg,
		BForm,
		BButton,
		VuexyLogo,
		ValidationProvider,
		ValidationObserver
	},
	mixins: [togglePasswordVisibility],
	data() {
		return {
			status: '',
			password: '',
			userEmail: '',
			sideImg: require('@/assets/images/pages/login-v2.svg'),
			// validation rulesimport store from '@/store/index'
			required,
			email,
			input_email: '',
			input_password: '',
			error_email: 0,
			error_password: 0,
			error_email_txt: '',
			error_password_txt: '',
			isPosting: false,
			input_code: '',
			input_invite_code: 'BGPoO5gw',
			error_email: 0,
			error_password: 0,
			error_code: 0,
			error_email_txt: '',
			error_password_txt: '',
			error_code_txt: '',
			regConf: {},
			countdown: 0,
			countdownThread: null,
			invite_code: null,
			show: true,
			count: '',
			timer: null
		}
	},
	computed: {
		passwordToggleIcon() {
			return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
		},
		imgUrl() {
			if (store.state.appConfig.layout.skin === 'dark') {
				// eslint-disable-next-line vue/no-side-effects-in-computed-properties
				this.sideImg = require('@/assets/images/pages/login-v2-dark.svg')
				return this.sideImg
			}
			return this.sideImg
		}
	},
	mounted() {
		if (this.$route.params.code) {
			if (this.$route.params.code.length > 4) {
				this.$cookies.set('invite_code', this.$route.params.code)
				this.$router.push({ name: 'Register' })
			}
		}
		//this.checkRegConf()
		if (this.$cookies.get('invite_code') && this.$cookies.get('invite_code').length > 4) {
			this.invite_code = this.$cookies.get('invite_code')
		}
		if (this.$cookies.get('isLogin') == 1) {
			this.$router.push({
				name: 'Dashboard'
			})
		}
	},
	methods: {
		getCode() {
			const TIME_COUNT = 60
			if (!this.timer) {
				this.count = TIME_COUNT
				this.show = false
				this.timer = setInterval(() => {
					if (this.count > 0 && this.count <= TIME_COUNT) {
						this.count--
					} else {
						this.show = true
						clearInterval(this.timer)
						this.timer = null
					}
				}, 1000)
			}
		},
		sendCode() {
			if (this.countdown !== 0) return
			if (this.input_email == null || this.input_email == '') {
				this.error_email = 1
				this.error_email_txt = ' - '
				err_count++
				return
			} else {
				if (!this.checkEmail(this.input_email)) {
					this.error_email = 2
					this.error_email_txt = ' - '
					err_count++
					return
				}
			}
			this.getCode()
			this.$http({
				method: 'POST',
				url: '/api/v1/passport/comm/sendEmailVerify',
				data: { email: this.input_email }
			})
				.then(res => {
					this.isPosting = false
					if (res.data.data == true) {
						this.countdown = 60
						this.countdownThread = setInterval(() => {
							if (this.countdown == 0) {
								clearInterval(this.countdownThread)
							} else {
								this.countdown = this.countdown - 1
								console.log(this.countdown)
							}
						}, 1000)
					}
				})
				.catch(err => {
					this.error_email = 3
					this.error_email_txt = ' - ' + err.response.data.message
				})
		},
		checkRegConf() {
			this.isPosting = true
			this.$http({
				method: 'GET',
				url: '/api/v1/passport/comm/config'
			})
				.then(res => {
					this.isPosting = false
					if (res.data.data) {
						this.regConf = res.data.data
					}
				})
				.catch(err => {
					alert('error')
				})
		},
		checkEmail(str) {
			var re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/
			return re.test(str)
		},

		reg() {
			if (this.isPosting) return
			this.isPosting = true
			let err_count = 0
			this.error_email = 0
			this.error_password = 0
			this.error_code = 0
			this.error_email_txt = ''
			this.error_password_txt = ''
			this.error_code_txt = ''
			console.log(this.input_email)

			if (this.input_code == null || this.input_code == '') {
				this.error_code = 1
				this.error_code_txt = ''
				this.$toast({
					component: ToastificationContent,
					props: {
						title: this.error_code_txt,
						icon: 'EditIcon',
						variant: 'danger'
					}
				})
				err_count++
			}
			if (this.input_email == null || this.input_email == '') {
				this.error_email = 1
				this.error_email_txt = ''
				this.$toast({
					component: ToastificationContent,
					props: {
						title: this.error_email_txt,
						icon: 'EditIcon',
						variant: 'danger'
					}
				})
				err_count++
			} else {
				if (!this.checkEmail(this.input_email)) {
					this.error_email = 2
					this.error_email_txt = ''
					this.$toast({
						component: ToastificationContent,
						props: {
							title: this.error_email_txt,
							icon: 'EditIcon',
							variant: 'danger'
						}
					})
					err_count++
				}
			}

			if (this.input_password == null || this.input_password == '') {
				this.error_password = 1
				this.error_password_txt = ''
				this.$toast({
					component: ToastificationContent,
					props: {
						title: this.error_password_txt,
						icon: 'EditIcon',
						variant: 'danger'
					}
				})
				err_count++
			} else {
				if (this.input_password.length < 8) {
					this.error_password = 2
					this.error_password_txt = '8'
					this.$toast({
						component: ToastificationContent,
						props: {
							title: this.error_password_txt,
							icon: 'EditIcon',
							variant: 'danger'
						}
					})
					err_count++
				}
			}

			if (err_count > 0) {
				this.isPosting = false
				return
			}

			let data = {
				email: this.input_email,
				password: this.input_password,
				email_code: this.input_code
			}
			if (this.invite_code !== null) {
				data.invite_code = this.invite_code
			} else {
				data.invite_code = this.input_invite_code
			}

			this.$http({
				method: 'post',
				url: '/api/v1/passport/auth/register',
				data: data
			})
				.then(res => {
					this.isPosting = false
					console.log(res)
					if (res.data.data == true) {
						this.$router.push({
							name: 'Login'
						})
					}
				})
				.catch(err => {
					this.error_email = 3
					this.error_email_txt = ' - ' + err.response.data.message
					this.$toast({
						component: ToastificationContent,
						props: {
							title: err.response.data.message,
							icon: 'EditIcon',
							variant: 'danger'
						}
					})
					this.isPosting = false
				})
		}
	}
}
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-auth.scss';
</style>
